.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 30rem;
  display: flex;
  flex-direction: column;
  overflow: scroll;
}
.cart-items .modal::-webkit-scrollbar {
  display: none;
}
.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
  color: rgb(173, 72, 0);
  align-items: right;
  margin: 1rem;
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  font-weight: bold;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #d519cc;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #8e0c92;
  border-color: #8e0c92;
  color: white;
}

.actions .button--close {
  color: #d519cc;
}

.actions .button {
  background-color: #d519cc;
  color: white;
}
@media (max-width: 400px) {
  .actions {
    text-align: center !important  ;
  }
  .actions .button {
    margin-top: 5px;
  }
}
